@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
body {
  margin: 0 auto;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.app__night {
  background-color: #191919;
  color: #fff;
  height: 100%;
}

.theme-button__img {
  width: 70px;
}

.theme-button {
  padding: 0;
  width: 100px;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

.navigation {
  display: -webkit-flex;
  display: flex;
  padding: 20px 0;
  font-family: 'Comic Neue', cursive;
  -webkit-align-items: center;
          align-items: center;
}

.navigation__nav {
  width: 390px;
  margin-left: auto;
}

.navigation__nav-list {
  list-style: none;
  padding: 0;
  padding-right: 30px;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.navigation__link {
  text-decoration: none;
  color: #484848;
  font-weight: bold;
  font-size: 35px;
}

.navigation__link_active {
  color: #cc272d;
}



.loading {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

.loading__title {
  margin: 0;
  margin-bottom: 25px;
  font-family: 'Comic Neue', cursive;
  color: #000;
}

.error-message {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

.error-message__title {
  margin: 0;
  font-family: 'Comic Neue', cursive;
  color: #000;
}

.main {
  font-family: 'Comic Neue', cursive;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.main__image {
  border-radius: 30%;
  display: block;
  max-width: 500px;
}

.main__container {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  margin-right: 100px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.main__title {
  margin: 0;
  color: #cc272d;
  font-weight: bold;
  font-size: 55px;
  margin-top: 80px;
}

.main__search-form {
  display: -webkit-flex;
  display: flex;
  margin-top: 30px;
}

.main__search-input {
  outline: none;
  padding: 0;
  padding-left: 25px;
  border-radius: 20px;
  font-size: 30px;
  font-family: 'Comic Neue', cursive;
  width: 587px;
  height: 67px;
  border: 1px solid #000;
}

.main__search-input::-webkit-input-placeholder {
  font-size: 28px;
  font-family: 'Comic Neue', cursive;
}

.main__search-input:-ms-input-placeholder {
  font-size: 28px;
  font-family: 'Comic Neue', cursive;
}

.main__search-input::placeholder {
  font-size: 28px;
  font-family: 'Comic Neue', cursive;
}

.main__button-search-link {
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  margin-left: 15px;
}

.main__button-search {
  background: none;
  outline: none;
  border: none;
}

.main__button-search-img {
  width: 70px;
}

.character {
  max-width: 188px;
  height: auto;
  max-height: 325px;
  background-color: #484848;
  border-radius: 20px;
}

.character_change_color {
  background-color: #cc272d;
}

.character__link {
  margin: 0;
  padding: 0;
  border: none;
  text-decoration: none;
}

.character__image {
  width: 100%;
  height: 180px;
  border-radius: 20px 20px 0 0;
}

.character__name-box {
  display: -webkit-flex;
  display: flex;
  min-height: 80px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.character__name {
  margin: 0;
  color: #fff;
  font-size: 21px;
  font-weight: bold;
}

.add-button {
  margin: 0 auto;
  display: block;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin-top: 15px;
  margin-bottom: 15px;
}

.add-button-img {
  width: 60px;
}

.characters_page__container {
  margin: 0 auto;
  padding: 20px 20px;
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: repeat(5, 1fr);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.characters_page__link {
  text-decoration: none;
  margin: 0;
}

.comics {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  gap: 30px;
  margin: 0 20px;
}

.comics-info__button {
  padding: 0;
  margin: 0;
  cursor: pointer;
  outline: none;
  background: none;
  border: none;
  z-index: 1000;
}

.comics-info {
  font-family: 'Comic Neue', cursive;
  border: 2px solid red;
  z-index: 1000;
}

.comics-info__name-box {
  background-color: #404040;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.comics-info__name {
  margin: 0;
  font-size: 20px;
  text-align: center;
  color: #cc272d;
  -webkit-align-self: center;
          align-self: center;
}

.comics-info__box-img-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.comics-info__image {
  max-width: 491px;
  height: 508px;
}

.comics-info__description {
  margin: 15px;
  font-size: 18px;
}

.popup {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  min-height: 100%;
  z-index: 1000;
}

.popup_hidden {
  display: none;
}

.popup__img-container {
  margin: 0 auto;
  margin-top: 50px;
  display: grid;
  width: 70%;
  grid-gap: 10px;
  gap: 10px;
  grid-template-columns: repeat(7, 1fr);
}

.popup__button {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 0;
  position: absolute;
  top: 10px;
  left: 160px;
  width: 40px;
  height: 40px;
}

